export default {
	data() {
		return {
			billId:'2c9a29b67992dc52017992df8b8c0000',
			historyInfo:{}
		};
	},
	onLoad(option){
		console.log(option)
		if(option.billId) {
			this.billId = option.billId
		}
		this.getBuyVipDetail()
	},
	methods:{
		// 获取当前页面的信息
		getBuyVipDetail(){
			this.$Request.get(`/appVipController.do?getBuyVipDetail&billId=${this.billId}`)
			.then(res => {
				if(res.code == 0){
					this.historyInfo = res.data
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
	}
}
